import * as fgs from "../../fgs";
import type { AdaptativeVisualIdentity } from "../../../adaptativeVisualIdentity";

export class InGameView extends fgs.InGameView {
	public constructor(protected vi: AdaptativeVisualIdentity) {
		super(vi);
		this.purse.formatOptions = { symbolFormatName: "postspace", minimumFractionDigits: 2 };
	}

	/**
	 * Called once the view is loaded
	 */
	public onLoad(): void {
		super.onLoad();
	}
}
